<template>
  <div id="forget-password">

    <!-- logo start -->
    <div class="logo">
      <img src="./images/logo.png" alt="">
    </div>
    <!-- logo end -->

    <!-- shop select start -->
    <div class="select-content">
      <div class="label">
        忘记密码
      </div>

      <div class="from-content">
        <el-form class="password-form" :model="passwordForm" label-width="87px">
          <div class="check-phone">
            <el-form-item label="登陆手机号">
              <el-input :class="{'errtip': isShowPhoneErr}" 
              v-model="passwordForm.loginPhone" 
              placeholder="输入登陆手机号"
              @keyup.native="checkPhoneNum"
              ></el-input>              
            </el-form-item>
            <span v-if="isShowPhoneErr" class="errMessage"> 手机号码输入错误</span>
          </div>

          
          <div class="check-code">
            <el-form-item label="图形验证码">
              <el-input :class="{'errtip': isShowcodeErr}" 
              v-model="passwordForm.code"
              placeholder="输入图形验证码"
              @change="clearCodetip"></el-input>
            </el-form-item>
            <div class="check-img" @click="changeCheckImg">
                <img :src="codeimgurl" alt="验证图片" />
            </div>
            <span v-if="isShowcodeErr" class="errMessage"> 图形验证码输入错误</span>
          </div>

          <div class="check-msg">
            <el-form-item label="短信验证码">
              <el-input :class="{'errtip': isShowPhonecodeErr}" 
              v-model="passwordForm.messagecode"
              placeholder="输入短信验证码"></el-input>
            </el-form-item>
            <div v-if="!isShowCheckGrey" class="check-message gray">{{checkMsgGray}}</div>
            <div v-else class="check-message" @click="sendPhoneNum">{{ checkMsg }}</div>
            <span v-if="isShowPhonecodeErr" class="errMessage"> 短信验证码输入错误</span>
          </div>

          <div>
            <el-form-item label="设置新密码">
              <el-input :class="{'errtip': isShowPasswordErr}"
              v-model="passwordForm.newPassword"
              placeholder="输入新密码"
              :type="pwdType"
              @change="clearPasswordtip"
              ></el-input>
            </el-form-item>
          </div>

          <div>
            <el-form-item label="确认新密码">
              <el-input :class="{'errtip': isShowPasswordErr}"
              v-model="passwordForm.newPasswordConfrim"
              placeholder="重复新密码"
              :type="pwdType"
              @blur="checkPassword"
              @change="clearPasswordtip"
              ></el-input>
            </el-form-item>
             <span v-if="isShowPasswordErr" class="errMessage"> 两次密码输入不一致</span>
          </div>
        </el-form>
      </div>

        <div class="btn-group">
          <div class="btn btn-confrim" @click="sendNewpassword">修改密码</div>
          <div class="btn btn-cancel" @click="cancelChangePassword">取消</div>
        </div>
    </div>
    <!-- shopo select end -->
    
    <!-- <sl-errtip></sl-errtip> -->
    
  </div>
</template>

<script>
// import SlErrtip from "@/components/SlErrtip";
import { Util } from "@/config/util";
import { Public } from "@/config/public";
import { getPhoneCode, changePassword } from "../api";
import { errcode } from "@/config/cfg";

export default {
  components: {
    // SlErrtip
  },
  data() {
    return {
      passwordForm: {
        loginPhone: "",
        code: "",
        messagecode: "",
        newPassword: "",
        newPasswordConfrim: ""
      },
      pwdType: "password",

      codeimgurl: "",
      checkMsgGray: "获取验证码",
      checkMsg: "获取验证码",
      isShowCheckGrey: false,

      isShowPhoneErr: false,
      isShowcodeErr: false,
      isShowPhonecodeErr: false,
      isShowPasswordErr: false,

      base_url: base_url
    };
  },
  mounted() {
    this.init();
    this.initCheckImg();
  },
  methods: {
    init() {},
    initCheckImg() {
      this.token = Util.creatToken();
      this.codeimgurl =
        base_url +
        "/code.php?height=26&width=100&fontsize=16&codelen=4&token=" +
        this.token +
        "&is_plain=1";
    },
    changeCheckImg() {
      this.codeimgurl =
        base_url +
        "/code.php?height=26&width=100&fontsize=16&codelen=4&token=" +
        this.token +
        "&is_plain=1" +
        "&" +
        Math.random();
    },
    //60s验证码
    setTimerDown() {
      let sec = 60;
      for (let i = 0; i <= 60; i++) {
        setTimeout(() => {
          if (sec !== 0) {
            // debugger;
            this.checkMsgGray = sec + "s后重新获取";
            sec--;
          } else {
            sec = 60; //如果倒计时结束就让  获取验证码显示出来
            this.isShowCheckGrey = true;
            this.checkMsg = "重新获取";
          }
        }, i * 1000);
      }
    },
    //手机号输入校验
    checkPhoneNum() {
      this.passwordForm.loginPhone = Util.checkRound(
        this.passwordForm.loginPhone
      );
      if (this.passwordForm.loginPhone !== "") {
        this.isShowCheckGrey = true;
      }
    },
    //校验手机号
    checkPhoneTrueNum() {
      return Public.checkPhoneNum(this.passwordForm.loginPhone);
    },
    sendPhoneNum() {
      if (this.checkPhoneTrueNum()) {
        let data = {
          get_coke: 1,
          page_code: this.passwordForm.code,
          phone: this.passwordForm.loginPhone,
          name: "forgetPwd"
        };

        getPhoneCode(data, resp => {
          if (resp.ret === 0) {
            this.isShowCheckGrey = false;
            this.setTimerDown();
            this.changeCheckImg();
          } else if (resp.ret === errcode.IMG_ERRL) {
            this.isShowcodeErr = true;
            this.changeCheckImg();
          } else {
            this.changeCheckImg();
            console.warn("获取验证码错误！");
          }
        });
      } else {
        console.log("请输入正确手机号！");
      }
    },
    cancelChangePassword() {
      this.$router.push("/");
    },
    checkPassword() {
      if (
        this.passwordForm.newPassword !== this.passwordForm.newPasswordConfrim
      ) {
        this.isShowPasswordErr = true;
      } else {
        this.isShowPasswordErr = false;
      }
    },
    clearPasswordtip() {
      this.isShowPasswordErr = false;
    },
    clearCodetip() {
      this.isShowcodeErr = false;
    },
    // 修改密码
    sendNewpassword() {
      let data = {
        save_new_passwd: 1,
        phone: this.passwordForm.loginPhone,
        phone_code: this.passwordForm.messagecode,
        new_passwd: this.passwordForm.newPassword,
        new_passwd_again: this.passwordForm.newPasswordConfrim
      };

      changePassword(data, resp => {
        if (resp.ret === 0) {
          this.$slnotify({
            message: "成功修改密码"
          });
          setTimeout(() => {
            this.$router.push("/");
          }, 5000);
        } else {
          console.warn("修改失败！");
        }
      });
    }
  }
};
</script>

<style lang="scss" scoped>
@import "src/styles/mixin.scss";

#forget-password {
  width: 100%;
  height: 100vh;
  background-image: url(./images/loadingpage.png);
  background-repeat: no-repeat;
  background-size: cover;
  position: relative;

  .logo {
    position: absolute;
    @include wh(212px, 72px);
    @include tl(50px, 60px);

    img {
      @include wh(212px, 72px);
    }
  }
  .select-content {
    height: 80%;
    background-color: #fff;
    position: absolute;
    left: 40px;
    right: 40px;
    top: 16%;
    box-shadow: 0 2px 4px 0 #becaeb;
    border-radius: 2px;

    .label {
      position: absolute;
      @include wh(230px, 50px);
      @include fc(20px, #fff);
      @include tl(50px,-40px);
      background-color: #4877e7;
      border-radius: 0 25px 25px 0;
      font-weight: bold;
      line-height: 50px;
      text-align: right;
      padding-right: 20px;
    }
    .from-content {
      margin-top: 150px;
      margin-left: 87px;
    }
  }
}
.password-form {
  width: 530px;
}
.check-code {
  position: relative;

  .check-img {
    border-radius: 2px;
    position: absolute;
    top: 4px;
    right: 167px;
    cursor: pointer;
  }
}

.check-msg {
  position: relative;

  .check-message {
    @include wh(100px, 24px);
    text-align: center;
    line-height: 24px;
    border-radius: 2px;
    position: absolute;
    top: 5px;
    right: 167px;
    cursor: pointer;
    background: #4877e7;
    color: #fff;
    font-size: 12px;
  }
  .gray {
    background: #d8d8d8;
  }
}

.errMessage {
  @include fc(12px, #e7487e);
  margin-left: 40px;
}
.btn-group {
  position: absolute;
  left: 50%;
  bottom: 50px;
  transform: translateX(-50%);
  .btn {
    @include wh(160px,40px);
    line-height: 40px;
    text-align: center;
    border-radius: 2px;
    font-size: 14px;
    color: #4877e7;
    display: inline-block;
    border: 1px solid #4877e7;
    cursor: pointer;
  }
  .btn-confrim {
    background: #4877e7;
    color: #fff;
    margin-right: 60px;
  }
}
</style>

<style lang="scss">
@import "src/styles/mixin.scss";

#forget-password {
  .el-input .el-input__inner {
    width: 280px;
    height: 34px;
    border-radius: 0;
  }
  .el-form-item__content {
    line-height: 34px;
  }
  .el-form-item__label {
    padding-top: 10px;
    padding-bottom: 10px;
  }
  .el-input.errtip .el-input__inner {
    border-color: #e7487e;
  }
  .el-form-item {
    display: inline-block;
  }
}
</style>






